import commonMixins from "@/mixins/common.js";
export default {
  mixins: [commonMixins],
  data() {
    return {};
  },
  head() {
    let title = "CLD | Deepika Godha";
    return {
      title: title,
      meta: [
        {
          hid: "description",
          name: "description",
          content: "",
        },
        {
          hid: "keywords",
          name: "keywords",
          content: "",
        },
        {
          name: "robots",
          content: `index,follow`,
        },
        {
          name: "viewport",
          content:
            "width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=0",
        },
      ],
      link: [
        {
          rel: "canonical",
          href: this.$route.path,
        },
      ],
      __dangerouslyDisableSanitizers: ["title", "meta"],
    };
  },
};
